/* ================================================================================= */
.headerPosition {
    position: fixed;
    display: flex;
    flex-direction: row;
    z-index:100;
}

.headerContainer{
    width: 100%;
    height:60px;
    top: 0;
    background-color:white;
    /* background-color: rgb(65,173,73); */
    padding: 5px;
    position: fixed;
    box-shadow:0 1px 6px rgba(2, 15, 7, 0.5); 
    z-index:102;
    /* border-bottom: 1px solid black; */
} 


  .B4Llogo {
    top:5px;
    left:25px;
    position: fixed;
    height: 55px;
    width:auto;
    z-index:500;
} 

.headerContainerSmallText{
    position:fixed;
    /* width:100%;
    height:30px;
    top: 10px; */
    right:25px;
    font-size:18px;
    display: flex;
    flex-direction:row;
    justify-content: flex-end;
    align-items: center;
    user-select: none;
    /* font-weight: 500; */
}

.imageContain{
    width:40px;
    height:40px;
    background-image: contain;
    cursor: pointer;
    font-size:30px;
    color: #0A558F;
    font-weight: bold;
    animation: fadeIn .75s forwards;
    text-align: center;
    padding: 10px 0px 10px 0px;
}

/* fadeIn animation moved to index.css */

@media screen and (min-width: 619px){
    .headerContainer{
        border-bottom: none;
    }
}

@media screen and (max-width: 399px){

    .B4Llogo{
        top:10px;
        height: 45px;
        left:10px;
    }

}